export default (state = {}, action) => {
	if (typeof state === 'undefined') return

	switch (action.type) {
		case 'SIGN_IN':
			localStorage.setItem(
				'profile',
				JSON.stringify({
					user: action?.payload?.user,
					token: action?.payload?.token,
				})
			)

			return {
				...state,
				user: action?.payload?.user,
				token: action?.payload?.token,
			}
		case 'SIGN_UP':
			return { ...state, user: action?.payload?.user }
		case '@@INIT':
			console.log('INIT')
			return state
		default:
			return state
	}
}
